@font-face {
  font-family: 'Game';
  src: url('../assets/sc.ttf') format('truetype');
  font-weight: normal;
  font-style: normal;
}

.title {
  font-family: "Game";
  color: yellow ;
  font-size: 5rem;
  text-shadow: 10px 10px 0px black;
  position: fixed;
}

.link {
  font-family: "Game";
  color: yellow;
  font-size: 1rem;
  text-shadow: 5px 5px 0px black;
  position: fixed;
}

body {
    padding: 0px;
    margin: 0px;
    overflow-y: scroll;
}



.bg {
    position: fixed;
    width: 100%;
    height: 100%;
    object-fit: cover;
    z-index: -1;
}

.screen {
    position: fixed;
    height: 100vh; // El 100% de la altura de la ventana
    width: calc(100vh * 0.5625); // 9:16 aspect ratio (9/16 = 0.5625)
    
    @media screen and (max-width: 576px) { // Para dispositivos móviles
      width: 100vw; // El 100% del ancho de la ventana
    }
  }

  .bottom {
    position: fixed;
    bottom: 0px;
    width: 100%; /* Ajusta el ancho según tus necesidades */
    height: 10vh; /* Ajusta la altura según tus necesidades */
    background-image: url("../assets/shapes/bottom.png"); /* Cambia la ruta a tu imagen */
    background-repeat: repeat-x; /* Repetir la imagen solo en el eje X */
    background-size: auto 100%; /* Ajusta el tamaño de la imagen */
  }